.header {
  width: 100%;
  min-height: 100%;
  background-color: $lightGray;
  padding-top: 40px;
  @media (max-width: 900px) {
    padding-top: 20px;
  }
  &__container {
    max-width: 980px;
    min-height: 100%;
    margin: 0 auto;
    
  }
  &__img-wrapper {
    width: 100%;
    margin-bottom: 20px;
    text-align: center;
  }
  &__logo {
    @media (max-width: 820px) {
      width: 392px;
      height: 65px;
    }
    @media (max-width: 600px) {
      margin-left: 10px;
      width: 258px;
      height: 43px;
    }
  }

  &__text {
    text-align: center;
    margin-bottom: 40px;
    // margin-right: 30px;
    @media (max-width: 900px) {
      margin-bottom: 20px;
    }
  }


  &__nav {
    min-height: 60px;
    display: flex;
    align-items: center;
    background-color: $darkGray;
    width: 100%;
    @media (max-width: 620px) {
      // display: none;
    }
  }
  &__nav-list {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    @media (max-width: 760px) {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
     }
  }
  &__nav-item {
    color: $gray;  
    &--active {
      color: $bordo;
    }
    &:hover {
      color: darken($color:$gray , $amount: 20%);
    }
    &:active {
      color: $bordo;
    }
  }
  &__li {
    text-transform: uppercase;
    margin: 0 40px;
    font-size: 12px;
    @media (max-width: 1000px) {
      margin: 0 20px;
    }
    @media (max-width: 760px) {
      margin: 5px 20px;
    }

  } 
  &__line {
    height: 5px;
    background-color: $darkGray;
    border: 1px solid $gray;
    border-right: none;
    border-left: none;
  }
}