.footer {
  width: 100%;
  min-height: 100%;
  background-color: $lightGray;
  padding-bottom: 30px;
  
  &__container {
    max-width: 980px;
    min-height: 100%;
    margin: 0 auto;
    
  }
  &__nav {
    min-height: 48px;
    display: flex;
    align-items: center;
    background-color: $darkGray;
    width: 100%;
  }
  &__nav-list {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    @media (max-width: 760px) {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin: 5px 0;
     }
  }
  &__nav-item {
    width: 100%;
    text-transform: uppercase;
    margin: 0 25px;
    font-size: 12px;
    color: $gray;  
    &:hover {
      color: darken($color:$gray , $amount: 20%);
    }
    &:active {
      color: $bordo;
    }
    @media (max-width: 1000px) {
      font-size: 12px;
      margin: 0 15px;
    }
    @media (max-width: 840px) {
      margin: 0 15px;
      font-size: 10px;
      display: block;
      width: 100%;
    }
    @media (max-width: 760px) {
      margin: 5px 0px;
    }
  }
  // &__li {
  //   // width: 100%;
  //   // text-transform: uppercase;
  //   // margin: 0 25px;
  //   // font-size: 12px;
  //   @media (max-width: 1000px) {
  //     font-size: 12px;
  //     margin: 0 15px;
  //   }
  //   @media (max-width: 770px) {
  //     font-size: 8px;
  //     display: block;
  //     width: 100%;
  //   }
  // } 
  &__line {
    height: 5px;
    background-color: $darkGray;
    border: 1px solid $gray;
    border-right: none;
    border-left: none;
  }
}


