.main-screen {
  background-color:$lightGray;

  &__container {
    padding-top: 30px;
    background-color: $white;
    max-width: 980px;
    min-height: 100%;
    margin: 0 auto;
    @media (max-width: 760px) {
      width: 100%;
     }
  }
  &__title-wrapper {
    margin: 0 24px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 16px;
    @media (max-width: 760px) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 0 px;
     }
  }
  &__search-title {
    text-transform: uppercase;
    font-family: $times;
    margin-bottom: 10px;
    font-weight: 600;
  }
  &__search-wrapper {
    flex-shrink: 0;
    @media (max-width: 760px) {
      margin: 0 auto;
      text-align: center;
      flex-shrink: 0.5;
      margin-bottom: 16px;
      width: 250px;
    }
  }

  
  &__search {
    max-width: 230px;
    height: 20px;
    display: flex;
    border: 1px solid $darkGray;
    border-radius: 10px;
    z-index: 10;
    flex-shrink: 0;
    margin-right: 60px;
    @media (max-width: 760px) {
      margin-right: 0px;
      max-width: 350px;
    }
  }
  
  &__input-wrapper {
    width: 85%;
    margin-right: 15px;
  }

  &__search-item {
    width: 100%;
    height: 100%;
    outline: none;
    margin-left: 10px;
    border:none;
    font-size: 12px;
    color: $darkGray;
  }

  &__search-btn {
    margin-top: 3px;  
    margin-right: 10px;
    outline: none;
    border:transparent;
    background: transparent;
    @media (max-width: 760px) {
      margin-right: 0px;
      margin-left: 0px;
    }
  }
  &__title {
    text-transform: uppercase;
    text-align: center;
    font-size: 18px;
    font-family: $times;
    font-weight: 400;
    margin-right: 20px;
    line-height: 1.2;
    margin: 0 auto;
    @media (max-width: 970px) {
      margin-right: 20px;
    }
    @media (max-width: 760px) {
      font-size: 14px;
      margin: 0 auto;
      line-height: 1.6;
    }

  }

  &__wrapper {
    display: flex;
    margin-bottom: 20px;
    @media (max-width: 760px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__column {
    width: 33.3%;
    margin-right: 30px;
    &:first-of-type {
      max-width: 270px;
    }
    &:last-of-type {
      margin-right: 0px;
    }
    @media (max-width: 760px) {
      margin: 0 auto;
      width: 100%;
      text-align: center;

    }  
  }

  &__sidebar {
    @media (max-width: 760px) {
      display: none;
    }  
  }
  &__product-list {
    display: flex;
    flex-direction: column;
  } 
  &__list-title-wrapper {
    max-width: 270px;
    min-height: 48px;
    background-color: $gray;
    padding-left: 20px;
    display: flex;
    align-items: center;
    font-weight: 600;
    font-family: $times;
    margin-bottom: 16px;
    

  }
  &__list-item {
    font-size: 12px;
    padding-left: 20px;
    font-family: $times;
    margin-bottom: 8px;
    &:hover {
      color: darken($color:$gray , $amount: 20%);
    }
    &--active {
      color: $bordo;  
    }
    &:last-of-type {
      margin-bottom: 16px;
    }
  }
  &__image-wrapper {
    width: 100%;
    margin-bottom: 20px;
  }
  &__image {
    width: 100%;
    height: 100%;
  }
  &__thumbnails-wrapper {
    display: flex;
    margin-bottom: 24px;
  }
  &__thumbnails-item {
    width: 33.3%;
    height: 100%;
  }
  &__description {
    margin-bottom: 14px;
    text-transform: uppercase;
    font-family: $times;
  }
  &__description-text {
    line-height: 1.8;
    @media (max-width: 670px) {
      max-width: 290px;
      margin: 0 auto;
      margin-bottom: 14px;
    }
  }
  &__code {
    padding-top: 16px;
    text-transform: uppercase;
    font-family: $times;
    margin-bottom: 20px;
    @media (max-width: 670px) {
      padding-top: 0px;
    }
  }
  &__price {
    text-transform: uppercase;
    font-family: $times;
    margin-bottom: 20px;
    color: $bordo;
  }
  &__our-price {
    text-transform: uppercase;
    font-family: $times;
    margin-bottom: 20px;
  }
  &__include {
    margin-bottom: 10px;
    @media (max-width: 670px) {
      font-weight: 600;
    }
  }
  &__include-text {
    max-width: 290px;
    line-height: 1.8;
    margin-bottom: 20px;
    @media (max-width: 1000px) {
      max-width: 230px;
    }
    @media (max-width: 760px) {
      max-width: 209px;
      margin: 0 auto;
    }
    @media (max-width: 670px) {
      max-width: 290px;
      margin: 0 auto;
      margin-bottom: 14px;
    }
  }

  &__button-wrapper {
    margin-right: 10px;
    
    @media (max-width: 600px) {
      margin-right: 0px;
    }

  }
  &__button-item {
    font-family: $times;
    font-size: 10px;  
    text-transform: uppercase;
    width: 144px;
    height: 30px;
    border: 1px solid $bordo;
    border-radius: 10px;
    color: $bordo;
    background-color: transparent;
    margin-bottom: 20px;
    &:hover {
      color: darken($color:$gray , $amount: 20%);
      border-color: darken($color:$gray , $amount: 20%);
    }
    &:active {
      background-color: $bordo;
      color: darken($color:$gray , $amount: 20%);
    }
    &--active {
      background-color: $bordo;
      color: $white;
    }
    &:nth-child(1) {
      margin-right: 14px;
    }
    &:nth-child(3) {
      margin-right: 14px;
    }
    @media (max-width: 1000px) {
      width: 110px;
    }
    @media (max-width: 840px) {
      width: 90px;
      font-size: 8px;
    
      &:nth-child(1) {
        margin-right: 6px;
      }
      &:nth-child(3) {
        margin-right: 6px;
      }
    }
    @media (max-width: 760px) {
      width: 120px;
      font-size: 8px;
      margin-right: 5px;
      &:nth-child(1) {
        margin-right: 5px;
      }
      &:nth-child(3) {
        margin-right: 5px;
      }
    }
  }
  &__information {
    text-transform: uppercase;
    font-family: $times;
    margin-bottom: 14px;
  }
  &__table {
    display: flex;
    @media (max-width: 760px) {
      justify-content: center;
    }
    &:nth-last-child  {
      border-right: 1px solid $gray ;
    }
  }
  

  &__table-column-left {
    &:first-child {
      border-top: 1px solid $gray 
    }
  }
  &__table-column {
    &:only-child {
      border-right: 1px solid $gray;
    }
    &:first-child {
      border-top: 1px solid $gray; 
    }
  }
  
  &__table-cell {
    font-size: 12px;
    font-family: $times;
    width: 142px;
    min-height: 22px;
    border: 1px solid $gray;
    display: flex;
    align-items: center;
    border-right: none;
    border-top: none;
    @media (max-width: 1000px) {
      max-width: 110px;
    }
    @media (max-width: 800px) {
      max-width: 104px;
    }
  }
  &__cell-text {
    padding-left: 12px;
  }
}